import React, { useState } from 'react'
import './Rss.scss'
import { DEFAULT_CORS_PROXY } from '../../../interfaces/interface';
import { extract } from '@extractus/feed-extractor'
import newsImg from "../../../assets/main/news.jpg";

type Props = {
    setShowRss: React.Dispatch<React.SetStateAction<boolean>>;
}


const Rss = ({ setShowRss }: Props) => {
    type RssItemType = {
        id: string;
        title: string;
        link: string;
        published: string;
        description: string;
        mediaUrl: string;
    }

    const buttons = [
        {
            id: 0,
            img: 'data:image/png;base64,',
            link: 'https://rss.nytimes.com/services/xml/rss/nyt/HomePage.xml',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content']
                    const mediaUrl = mediaContent ? mediaContent['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 1,
            img: 'data:image/png;base64,',
            link: 'https://abcnews.go.com/abcnews/usheadlines',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaThumbnail = entryData['media:thumbnail']
                    const mediaUrl = mediaThumbnail ? mediaThumbnail[mediaThumbnail.length - 1]['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 2,
            img: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAIAAAD8GO2jAAAAjUlEQVR4AWP4Q2NAZQtGLRi14O/fv/8pBkBDaGQBAhCy4PmK/6dY/5+VRUcnGCD6GXiDpXXi0RCDdMjEaSuJ88HTpUCzgHago2MwCxicGcSD0BGD94SpK6hlgRvQveiIwRvoA1pbMGrBqAWjFgz+ouIE3AJvBulwBhlUxOBPrA8oBzSvcIZYnTxqwagFAH35Im3PxoFeAAAAAElFTkSuQmCC',
            link: 'https://www.scmp.com/rss/91/feed',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaThumbnail = entryData['media:thumbnail']
                    const mediaUrl = mediaThumbnail ? mediaThumbnail['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 3,
            img: 'data:image/png;base64,',
            link: 'https://moxie.foxnews.com/google-publisher/latest.xml',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content']
                    const mediaUrl = mediaContent ? mediaContent['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 4,
            img: 'data:image/png;base64,',
            link: 'https://www.forbes.com/real-time/feed2/',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content']['media:thumbnail']
                    const mediaUrl = mediaContent ? mediaContent['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 5,
            img: 'data:image/png;base64,',
            link: 'https://www.theguardian.com/us-news/rss',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content']
                    const mediaUrl = mediaContent ? mediaContent[mediaContent.length - 1]['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 6,
            img: 'data:image/png;base64,',
            link: 'https://feeds.nbcnews.com/nbcnews/public/world',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content'][0]
                    const mediaUrl = mediaContent ? mediaContent['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
        {
            id: 7,
            img: 'data:image/png;base64,',
            link: 'https://www.latimes.com/local/rss2.0.xml',
            options: {
                getExtraEntryFields: (entryData: any) => {
                    const mediaContent = entryData['media:content']
                    const mediaUrl = mediaContent ? mediaContent['@_url'] : null
                    return { mediaUrl }
                }
            }
        },
    ]

    const handleFetchRss = async (id: number) => {
        const baseUrl = DEFAULT_CORS_PROXY + buttons[id].link;
        const result = await extract(baseUrl, buttons[id].options)
        if (result.entries) setRssData(result.entries)
    }

    const countTime = (time: string): string => {
        let dt1 = new Date(time);
        let dt2 = new Date();
        let diff = dt2.getTime() - dt1.getTime();
        let hours = Math.floor(diff / (1000 * 60 * 60));
        let diffRes = hours + 1 + " hours ago";
        if (hours >= 24) {
            let days = Math.floor(hours / 24);
            diffRes = days + " day ago";
        }
        return diffRes;
    }

    const RssItem = ({ item }: { item: RssItemType }) => <div className='rssItem'>
        <div className='rssItemImg'>
            <img alt='...' src={item.mediaUrl ? item.mediaUrl : newsImg} />
        </div>
        <div className='rssItemContent'>
            <p className='rssItemDate'>{countTime(item.published)}</p>
            <a href={item.link} target="_blank">
                <p className="rssItemTitle">{item.title}</p>
            </a>
            <p className='rssItemDescription'>{item.description}</p>
        </div>
    </div>

    const [rssData, setRssData] = useState<any>([])


    return (
        <div className='rss'>
            <div className='rssTop'>
                {
                    buttons.map((item, index) => {
                        return <button key={index} className='rssBtn' onClick={() => { handleFetchRss(index) }}>
                            <img src={item.img} />
                        </button>
                    })
                }
                <button className='rssBtnClose' onClick={() => { setShowRss(false) }}>
                    <i className="bx bx-x"></i>
                </button>
            </div>
            <div className='definitionBody'>

                {
                    rssData && rssData.map((el: RssItemType, id: number) => {
                        return <RssItem item={el} key={id} />
                    })
                }
            </div>
        </div>
    )
}

export default Rss